input {
  box-sizing: border-box;
}
input::placeholder  {
  color: #A8B0B9;
}
.Add-bg{
  width: 100%;
  max-width: 428px;
  height: auto;
  background: #FFFFFF;
  /* box-shadow: 0px -1px 0.5px rgba(0, 0, 0, 0.2);
  border-radius: 40px 40px 0px 0px; */
  margin: 0 auto; 
  position: relative;
}
.header {
  width: 100%;
  max-width: 428px;
  height: 70px;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  border-bottom: 1px solid #D5D5D5;
  justify-content: center;
  position: relative;
}
.header > span {
  position: absolute;
  left: calc((0% + 54.77px) / 2);
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  cursor: pointer;
}
.header > h2 {
  font-weight: 700;
  font-size: 18px;
  line-height: 21px;
  text-align: center;
  color: #000000;
}
.setting-bg {
  width: 100%;
  max-width: 428px;
  height: auto;
  background-color: #FFFFFF;
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
}
.setting-bg .setting-name {
  margin-top: 40px;
  width: 90%;
  max-width: 370px;
  height: 90px;
}
.setting-bg .setting-name p {
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  color: #000;
  margin-bottom: 30px;
}
.setting-bg .setting-name > input[type=text] {
  width: 100%;
  max-width: 360px;
  text-align: center;
  font-weight: 300;
  font-size: 18px;
  border: none;
  border-bottom: 1px solid #D4D4D4;
  padding: 8px;
}
.setting-bg .setting-name > input[type=text]:focus {
  outline: none;
  border-bottom: 2px solid #339DFC;
}
.setting-bg .setting-amount {
  margin-top: 40px;
  width: 90%;
  max-width: 370px;
  height: 90px;
}
.setting-bg .setting-amount p {
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  color: #000;
  margin-bottom: 30px;
}
.setting-bg .setting-amount .row {
  width: 100%;
  max-width: 370px;
  height: 40px;
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: center;
  position: relative;
}
.setting-bg .setting-amount .row > input[type=number] {
  width: 100%;
  max-width: 360px;
  text-align: center;
  font-weight: 300;
  font-size: 18px;
  border: none;
  border-bottom: 1px solid #D4D4D4;
  padding: 8px;
}
.setting-bg .setting-amount .row > input[type=number]:focus {
  outline: none;
  border-bottom: 2px solid #339DFC;
}
.setting-bg .setting-amount .row > select {
  height: 30px;
  position: absolute;
  right: 0;
  border: transparent;
  text-align: right;
  font-weight: 300;
  font-size: 18px;
  padding: 0px;
  cursor: pointer;
  background-color: transparent;
}
.setting-bg .setting-amount .row > select:focus {
  outline: none;
}
.setting-bg .setting-color {
  width: 90%;
  max-width: 370px;
  height: 175px;
  margin-top: 40px;
}
.setting-bg .setting-color p {
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  color: #000;
  margin-bottom: 30px;
}
.setting-bg .setting-color .color-box {
  width: 100%;
  max-width: 360px;
  height: 125px;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  align-content: space-between;
  margin: 0 auto;
}
.setting-bg .setting-color .color-box .color {
  width: 55px;
  height: 55px;
  border-radius: 30px;
  cursor: pointer;
}
.setting-bg .setting-color .color-box .color5 {
  height: 15px;
  flex-basis: 100%;
  overflow: hidden;
  border: none !important;
  cursor: default !important;
}
.setting-bg .setting-day {
  width: 90%;
  max-width: 370px;
  height: 90px;
  margin-top: 40px;
}
.setting-bg .setting-day p {
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  color: #000;
  margin-bottom: 30px;
}
.setting-bg .setting-day .days {
  width: 100%;
  max-width: 370px;
  height: 30px;
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
}
.setting-bg .setting-day .days > li {
  width:calc(100% /7);;
  height: 30px;
  text-align: center;
  font-size: 18px;
  color: #898E94;
  padding: 4px 0px;
  box-sizing: border-box;
  position: relative;
  cursor: pointer;
}
.setting-bg .setting-time {
  width: 90%;
  max-width: 370px;
  height: 101px;
  margin-top: 40px;
}
.setting-bg .setting-time p {
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  color: #000;
  margin-bottom: 30px;
}
.setting-bg .setting-time .time-box {
  width: 250px;
  height: 50px;
  border: 1px solid #000000;
  box-sizing: border-box;
  border-radius: 25px;
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-around;
  align-items: center;
  margin: 0 auto;
  cursor: pointer;
}
.setting-bg .setting-time .time-box span {
  width: 60px;
  flex-grow: 0;
  font-weight: 400;
  font-size: 24px;
  color: #000000;
  text-align: center;
  flex-grow: 1;
}
.setting-bg .setting-time .time-box span:nth-child(1) {
  width: 110px;
}
.setting-bg .setting-time .time-box span:nth-child(3) {
  width: 10px;
}
.setting-bg .setting-btn-box {
  margin-top: 60px;
  margin-bottom: 30px;
  width: 330px;
  height: 40px;
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
}
.setting-bg .setting-btn-box > button {
  width: 250px;
  height: 40px;
  border-radius: 20px;
  color: #fff;
  font-weight: 700;
  font-size: 16px;
  cursor: pointer;
}
.submit {
  background: #399DFC;
}
/* 요일 클릭시 효과 */
.days-animation {
  color: #399DFC !important;
}
.days-animation::after {
  content: '';
  position: absolute;
  top: calc((100% - 30px) / 2);
  left: calc((100% - 30px) / 2);
  width: 30px;
  height: 30px;
  background:transparent;
  border: 1px solid #339DFC;
  border-radius: 15px;
}
/* 색상 클릭시 효과 */
.color-animation {
  border: 3px solid #0E2624;
}

/* 시간 설정 박스 */
.time-set-box-bg {
  width: 100%;
  max-width: 428px;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
  position: absolute;
  top: 0;
}
.time-set-box {
  position: absolute;
  width: 100%;
  max-width: 428px;
  height: 334px;
  bottom: 0px;
  background: #FFFFFF;
  border-radius: 30px 30px 0px 0px;
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
  padding: 20px 50px 30px 50px;
  box-sizing: border-box;
}
.time-set-box .list-group {
  width: 330px;
  height: 120px;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  justify-content: center;
  margin-top: 40px;
  font-size: 24px;
}
.time-set-box .list-group select {
  width: 80px;
  height: 40px;
  font-size: 24px;
  border: none;
  text-align: center;
  -webkit-appearance:none; /* for chrome */
  -moz-appearance:none; /*for firefox*/
  appearance:none;
  background-color: transparent;
}
.time-set-box .list-group select:focus {
  outline: none;
}
.time-set-box .list-group .noon-box {
  margin-right: 30px;
}
.time-set-box .list-group .colon-box {
  width: 5px;
  margin-left: 5px;
  margin-right: 5px;
}
.time-set-btn-box{
  margin-top: 50px;
  width: 330px;
  height: 40px;
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
}
.time-set-btn-box > button{
    width: 250px;
    height: 40px;
    border-radius: 20px;
    color: #fff;
    font-weight: 700;
    font-size: 16px;
    cursor: pointer;
}
