@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');
body{
  font-family: 'Roboto', sans-serif;
  background-color: #E5E5E5;
}
.App-calendar {
  width: 100%;
  max-width: 428px;
  height: auto;
  min-height: 726px;
  background-color: #F0F2F5;
  margin: 0 auto;
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
}
.calendar-header {
  width: 100%;
  max-width: 428px;
  height: 85px;
  padding: 20px 0px 0px 15px;
  background-color: #fff;
}
.calendar-header h2{
  font-style: normal;
  font-weight: 700;
  font-size: 28px;
  color: #399DFC;
}
.calendar {
  width: 100%;
  max-width: 428px;
  height: 200px;
  margin-bottom: 40px;
}
.calendar .month {
  width: 100%;
  max-width: 428px;
  height: 70px;
  background-color: #fff;
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-around;
  align-items: center;
}
.calendar .month p{
  width: 40%;
  text-align: center;
}
.calendar .month button{
  width: 30%;
  max-width: 150px;
  background: transparent;
  border: none;
  box-sizing: border-box;
}
.calendar .month .icon-angle{
  width: 20px;
  height: 20px;
  color: #000;
}
.calendar .month p{
  font-size: 24px;
}
.week {
  width: 100%;
  max-width: 428px;
  height: 130px;
  background-color: #fff;
  border-radius: 0px 0px 20px 20px;
  box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.25);
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
  justify-content: space-between;
}
.week .week-string {
  width: 100%;
  max-width: 420px;
  height: 40px;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
}
.week .week-string > li {
  width: calc(100% / 7);
  height: 40px;
  background-color: #F0F2F5;
  display: flex;
  flex-flow: row;
  justify-content: center;
  align-items: center;
  padding: 2px 0px;
  box-sizing: border-box;
  font-weight: 300;
  font-size: 18px;
  color: #898E94;
  position: relative;
}
.week .week-number {
  width: 100%;
  max-width: 420px;
  height: 80px;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
}
.week .week-number > li {
  width: calc(100% / 7);
  height: 60px;
  background: #FFFFFF;
  font-weight: 500;
  font-size: 18px;
  color: #A8B0B9;
  padding: 0px;
  box-sizing: border-box;
  display: flex;
  flex-flow: row;
  justify-content: center;
  align-items: center;
  border-radius: 30px;
  position: relative;
  cursor: pointer;
}
/*오늘에 해당외는 날짜 표기*/
.is-to-day {
  color: #fff !important;
  z-index: 999;
}
.is-to-day::before {
  content: '';
  position: absolute;
  top: calc((100% - 30px) / 2);
  left: calc((100% - 30px) / 2);
  width: 30px;
  height: 30px;
  background:#1D90FC;
  border: none;
  border-radius: 15px;
  z-index: -999;
}
/*날짜를 클릭하면 나오는 날짜 표기*/
.click-day {
  color: #fff !important;
  z-index: 999;
}
.click-day::before {
  content: '';
  position: absolute;
  top: calc((100% - 30px) / 2);
  left: calc((100% - 30px) / 2);
  width: 30px;
  height: 30px;
  background:#FFB233;
  border: none;
  border-radius: 15px;
  z-index: -999;
}
