div{
  box-sizing: border-box;
  padding: 0;
  margin: 0ex;
}
.amount-bg {
  width: 95%;
  max-width: 400px;
  height: auto;
  min-height: 538px;
  background: #FFFFFF;
  border-radius: 20px;
  padding-top: 20px;
  padding-left: 10px;
  padding-right: 10px;
  margin: 0 auto;
  overflow-y: hidden;
  position: relative;
}
.amount-bg h2 {
  font-weight: 700;
  font-size: 28px;
  color: #000000;
  margin-left: 5px;
  margin-bottom: 20px;
  display: inline-block;
}
.vitamin-kind {
  width: 100%;
  max-width: 380px;
  height: 94.5px;
  background: #FFFFFF;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.25);
  border-radius: 15px;
  margin-bottom: 20px;
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
  justify-content: space-around;
  padding: 10px;
}
.vitamin-kind .color-name-box{
  width: 100%;
  max-width: 360px;
  height: 28px;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
}
.vitamin-kind .color-name-box p {
  font-weight: 700;
  font-size: 22px;
  color: #000000;
  margin-left: 20px;
}
.vitamin-color {
  width: 24px;
  height: 24px;
  border-radius: 12px;
}
.vitamin-kind .vitamin-amount-box{
  width: 100%;
  max-width: 360px;
  height: 28px;
  background-color: #C4C4C4;
  border-radius: 14px;
  position: relative;
}
.vitamin-kind .vitamin-amount-box .vitamin-amount-animation{
  width: 0%;
  height: 28px;
  background-color: #FBD757;
  border-radius: 14px;
  position: absolute;
  top: 0;
  right: 0;
  /* animation: Baranimation 1s 1 forwards; */
}
.vitamin-kind .vitamin-amount-box span {
  position: absolute;
  width: 360px;
  top: 3px;
  right: 15px;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  text-align: right;
}
@media (320px <= width <= 375px) {
  .amount-bg {
    border-radius: 10px;
  }
  .amount-bg h2 {
    font-weight: 700;
    font-size: 22px;
    color: #000000;
    margin-left: 7px;
    margin-bottom: 20px;
    display: inline-block;
  }
  .vitamin-kind {
    border-radius: 10px;
  }
  .vitamin-kind .color-name-box p {
    font-weight: 700;
    font-size: 20px;
    color: #000000;
    margin-left: 20px;
  }
  .vitamin-kind .vitamin-amount-box span {
    position: absolute;
    width: 360px;
    top: 4.5px;
    right: 15px;
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    text-align: right;
  }
}